import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApplicationService } from '@c8y/client';
import { ActionBarFactory, ActionBarItem } from '@c8y/ngx-components';
import { get } from 'lodash-es';
import { RefreshActionComponent } from './refresh-action.component';

@Injectable()
export class ImpactActionFactory implements ActionBarFactory {
  private static readonly applicationName = 'impact';

  constructor(private applicationService: ApplicationService) {}

  async get(activeRoute?: ActivatedRoute) {
    const actions: ActionBarItem[] = [];

    const data =
      !activeRoute.parent || activeRoute.snapshot.data.context
        ? activeRoute.snapshot.data
        : activeRoute.parent.snapshot.data;
    const { contextData } = data;

    const isDeviceInfoTab: boolean = get(activeRoute, 'snapshot.url[0].path') === 'device-info';

    const showRefreshActionButton: boolean =
      isDeviceInfoTab &&
      contextData &&
      contextData.c8y_ImpactResourceInfo &&
      /* call application service only for relevant devices to reduce number of service calls! */
      (await this.applicationService.isAvailable(ImpactActionFactory.applicationName)).data;

    if (showRefreshActionButton) {
      actions.push({
        priority: 500,
        placement: 'right',
        template: RefreshActionComponent
      } as ActionBarItem);
    }

    return actions;
  }
}
