import { Component, OnInit, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { IFetchResponse } from '@c8y/client';
import { AlertService, gettext } from '@c8y/ngx-components';
import { ImpactSubscriptionService } from './impact-subscription.service';

@Component({
  selector: 'c8y-impact-refresh-action',
  templateUrl: './refresh-action.component.html'
})
export class RefreshActionComponent implements OnInit {
  @ViewChild('templateCopy', { read: TemplateRef, static: true }) templateCopy;
  requestInProgress: boolean;

  constructor(
    private vcRef: ViewContainerRef,
    private router: Router,
    private impactService: ImpactSubscriptionService,
    private alert: AlertService
  ) {}

  ngOnInit() {
    this.vcRef.createEmbeddedView(this.templateCopy);
  }

  async refresh() {
    // TODO This is only a dirty hack to retrieve deviceId from URL;
    // In fact contextData should be provided for this component by a resolver?
    const url: string =
      this.router &&
      this.router.routerState &&
      this.router.routerState.snapshot &&
      this.router.routerState.snapshot.url;
    const deviceId: string = url && (/^\/device\/(\d+)\/.*$/gi.exec(url) || [])[1];

    if (deviceId) {
      this.requestInProgress = true;
      try {
        const res: IFetchResponse = await this.impactService.refreshDeviceResources(deviceId);
        if (res && res.status !== 200) {
          const data = res.json ? await res.json() : undefined;
          this.alert.addServerFailure({ data, res });
        } else {
          this.alert.success(gettext('Device resource refresh scheduled.'));
        }
      } catch (ex) {
        this.alert.addServerFailure(ex);
      }
      this.requestInProgress = false;
    } else {
      this.alert.danger(gettext('Could not find device ID in URL.'));
    }
  }
}
