import { Injectable } from '@angular/core';
import { FetchClient, IFetchOptions, IFetchResponse } from '@c8y/client';

@Injectable()
export class ImpactSubscriptionService {
  private readonly microserviceUrl: string = '/service/impact';
  private readonly header: any = { 'Content-Type': 'application/json' };

  constructor(private client: FetchClient) {}

  refreshDeviceResources(deviceId: string | number): Promise<IFetchResponse> {
    const options: IFetchOptions = {
      method: 'PUT',
      headers: this.header
    };
    return this.client.fetch(`${this.microserviceUrl}/refresh/${deviceId}`, options);
  }
}
